.slider__next-exclusive,
.slider__prev-exclusive {
	@include s(arrow-exclusive);
	width: 26px;
	height: 49px;
	position: absolute;
	top: 50%;
	margin-top: -43px;
	z-index: 20;
	@include r(500) {
		width: 0;
		height: 0;
		left: auto;
		right: 50%;
		display: none; } }
.slider__prev-exclusive {
	left: -20px;
	@include r(600) {
		left: -25px; } }
.slider__next-exclusive {
	right: -20px;
	transform: rotate(180deg);
	@include r(600) {
		right: -25px; } }
.exclusive__slides .slick-dots {
	margin: 10px auto 0;
	text-align: center;
	li {
		display: inline-block;
		margin-right: 10px; }
	button {
		font-size: 0;
		width: 15px;
		height: 15px;
		border-radius: 50%;
		background-color: #fff;
		border: 2px solid #000;
		& .active, &:hover {
			background-color: #000; } } }
.exclusive {
	padding: 40px 0 80px;
	@include r(790) {
		padding: 30px 0; }
	@include r(320) {
		padding: 20px 0; }
	&__title {
		color: #2d2d2d;
		font-family: Akrobat;
		font-size: 50px;
		text-transform: uppercase;
		text-align: center;
		margin-bottom: 30px;
		@include r(790) {
			font-size: 40px;
			margin-bottom: 20px; }
		@include r(500) {
			font-size: 30px;
			margin-bottom: 10px; }
		@include r(320) {
			font-size: 20px;
			margin-bottom: 5px; } }
	&__slider {
		position: relative; }
	&__slide {
		width: 50px; }
	&__slide-wrapper {
		margin: 0 10px; }
	&__image {
		position: relative;
		margin-bottom: 20px;
		overflow: hidden;
		&:hover img {
			transform: scale(1.1); }
		@include r(600) {
			margin-bottom: 10px; }
		img {
			width: 100%; }
		&:after {
			display: block;
			content: '';
			position: absolute;
			top: 10px;
			bottom: 10px;
			right: 10px;
			left: 10px;
			border: 1px solid #fff; } }
	&__name {
		color: #2d2d2d;
		font-family: Akrobatbold;
		/*semi bold*/
		font-size: 20px;
		text-transform: uppercase;
		margin-bottom: 5px;
		@include r(768) {
			font-size: 18px; }
		@include r(790) {
			font-size: 16px; } }
	&__price {
		color: #2d2d2d;
		font-family: Akrobat;
		font-size: 16px;
		text-transform: uppercase;
		font-family: Akrobatbold;
		@include r(790) {
			font-size: 14px; }
		span {
			font-size: 26px;
			color: #d24a43;
			@include r(790) {
				font-size: 16px; } } } }
