.features {
	padding: 40px 0;
	color: #000;
	@include r(820) {
		padding: 30px 0; }
	@include r(700) {
		padding: 20px 0; }
	@include r(500) {
		padding: 10px 0; }
	&__list {
		margin: 0 auto;
		display: flex;
		justify-content: center;
		align-items: baseline;
		align-content: center; }
	&__item {
		text-align: center;
		& + .features__item {
			margin-left: 80px;
			@include r(700) {
				margin-left: 60px; }
			@include r(580) {
				margin-left: 40px; }
			@include r(460) {
				margin-left: 5px; } }
		p {
			color: #000000;
			font-family: Akrobat;
			font-size: 20px;
			font-weight: 300;
			text-transform: uppercase;
			text-align: center;
			@include r(900) {
				font-size: 18px; }
			@include r(700) {
				font-size: 12px; }
			@include r(500); }
		.icon {
			font-size: 40px;
			margin-bottom: 15px;
			fill: #000;
			@include r(820) {
				font-size: 36px;
				margin-bottom: 10px; }
			@include r(700) {
				font-size: 32px;
				margin-bottom: 5px; }
			@include r(500) {
				font-size: 26px;
				margin-bottom: 2px; }
			svg {
				fill: #000; } } } }
