.small-woods {
	@include r(700) {
		padding: 20px 0; }
	.hide {
		@include r(500) {
			display: none; } }
	.container {
		text-align: center; }
	&__item {
		position: relative;
		&:after {
			display: block;
			content: '';
			position: absolute;
			@include s(fir-tree);
			bottom: 111px;
			left: 171px;
			transform: scale(1.4);
			border-bottom: 4px solid #65d499;
			border-radius: 10px;
			@include r(900) {
				display: none; } } }
	&__text {
		font-size: 27px;
		padding-left: 20px;
		color: #000000;
		font-family: Akrobat;
		font-weight: 400;
		text-transform: uppercase;
		margin-bottom: 10px;
		@include r(700) {
			font-size: 16px;
			margin-bottom: 0; }
		@include r(500) {
			padding: 0;
			text-align: left; } }
	&__wrap {
		position: relative;
		overflow: hidden;
		&:hover img {
			transform: scale(1.1); }
		&:after {
			position: absolute;
			display: block;
			content: '';
			left: 10px;
			right: 10px;
			top: 10px;
			bottom: 10px;
			border: 1px solid #fff; } }
	&__image {
		width: 100%;
		position: absolute;
		top: -100px;
		right: 100%;
		margin-right: -30px;
		&_bottom {
			right: auto;
			left: 100%;
			top: auto;
			bottom: -100px;
			margin-right: 0;
			margin-left: -30px;
			@include r(900) {
				left: auto;
				right: 100%;
				margin-left: 0;
				margin-right: -30px; }
			.small-woods {
				&__sale {
					left: auto;
					right: -35px;
					top: auto;
					bottom: -35px;
					@include r(700) {
						right: auto;
						bottom: -22px;
						left: -22px; }
					@include r(500) {
						left: 0;
						bottom: 0; } }
				&__text {
					padding-left: auto;
					padding-right: 20px;
					@include r(500) {
						padding: 0; } } } }
		img {
			width: 100%; } }
	&__sale {
		position: absolute;
		width: 70px;
		height: 70px;
		border: 2px solid #7b7b7b;
		background-color: rgba(255,255,255, .7);
		color: #d24a43;
		font-family: Akrobatbold;
		font-size: 37px;
		font-weight: bold;
		text-transform: uppercase;
		letter-spacing: 2.28px;
		top: 0px;
		left: -35px;
		line-height: 66px;
		text-align: center;
		@include r(700) {
			width: 45px;
			height: 45px;
			border: 1px solid #7b7b7b;
			font-size: 16px;
			line-height: 43px;
			left: -22px; }
		@include r(500) {
			top: 20px;
			left: 0px; } }

	&__stock {
		display: inline-block;
		position: relative;
		text-align: center;
		border: 1px solid #000000;
		padding: 0 50px;
		max-width: 300px;
		left: -15px;
		@include r(900) {
			transform: translate(50%, 0); }
		@include r(700) {
			max-width: 200px; }
		@include r(400) {
			width: 160px; } }
	&__loz {
		position: absolute;
		right: -250px;
		top: -100px;
		color: #d24a43;
		font-size: 30px;
		font-family: KobzarKS;
		font-weight: 400;
		letter-spacing: 1.32px;
		transform: rotate(15deg);
		text-align: center;
		width: 300px;
		@include r(900) {
			display: none;
			right: 0;
			font-size: 22px; }
		@include r(700) {
			right: -100px;
			font-size: 20px; }
		@include r(500) {
			font-size: 18px; } }
	&__payment {
		max-width: 270px;
		display: none;
		position: absolute;
		bottom: -40px;
		right: 50%;
		transform: translate(50%, 0);
		@include r(900) {
			display: block; }
		img {
			width: 100%; } } }
.stock-sw {
	margin: 160px 0;
	padding: 40px 50px;
	background-color: #808080;
	@include r(700) {
		padding: 20px 30px;
		margin: 100px 0; }
	@include r(500) {
		padding: 10px 0px; }
	&__title {
		text-align: center;
		display: block;
		color: #ffffff;
		font-family: Akrobatbold;
		font-size: 42px;
		text-transform: uppercase;
		/* Text style for "" */
		letter-spacing: 2.38px;
		@include r(700) {
			font-size: 20px;
			margin-bottom: 5px;
			line-height: 1; }
		&:after {
			display: block;
			content: '';
			width: 190px;
			height: 2px;
			background-color: #3eb5f1;
			margin: 10px auto;
			@include r(700) {
				margin: 8px auto 8px;
				display: none; } } }
	&__only {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 21px;
		font-weight: 400;
		text-transform: uppercase;
		/* Text style for ",  " */
		letter-spacing: 1.2px;
		/* Text style for "" */
		letter-spacing: 0.9px;
		margin-bottom: 10px;
		@include r(700) {
			font-size: 14px;
			margin-bottom: 5px; }
		@include r(500) {
			font-size: 10px; } }

	&__old-price {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 26px;
		font-weight: 400;
		text-decoration: line-through;
		text-transform: uppercase;
		/* Text style for "10 000" */
		letter-spacing: 1.71px;
		margin-bottom: 5px {
			font-size: 14px;
			margin-bottom: 5px; } }
	&__price {
		text-shadow: 3px 4px 5px rgba(0, 0, 0, 0.15);
		color: #fd7979;
		font-family: Akrobat;
		font-size: 37px;
		font-weight: 400;
		text-transform: uppercase;
		/* Text style for "5, 000" */
		letter-spacing: 2.38px;
		margin-bottom: 10px;
		@include r(700) {
			font-size: 18px;
			margin-bottom: 5px; } }
	&__sale {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 20px;
		font-weight: 400;
		text-transform: uppercase;
		/* Text style for ",  " */
		letter-spacing: 0.86px;
		margin-bottom: 40px;
		@include r(700) {
			font-size: 14px;
			margin-bottom: 30px;
			padding: 0 20px; }
		@include r(500) {
			font-size: 10px; }
		@include r(460) {
			margin-bottom: 10px; } }
	&__button {
		background-color: transparent;
		border: 2px solid #ffd357;
		padding: 14px;
		border-radius: 20px;
		color: #ffffff;
		font-family: Akrobat;
		font-size: 18px;
		font-weight: bold;
		text-transform: uppercase;
		/* Text style for " " */
		letter-spacing: 1.17px;
		@include r(700) {
			font-size: 14px;
			padding: 5px 10px; }
		@include r(500) {
			font-size: 12px;
			border-radius: 10px; } } }
