.footer {
	padding-top: 40px;
	background-color: #525252;
	.container {
		position: relative; }
	&__wrap {
		display: flex;
		justify-content: space-between;
		color: #dcdcdc;
		font-family: Akrobat;
		font-size: 16px;
		// Text style for "  ,"
		@include r(720) {
			-webkit-justify-content: center;
			justify-content: center; } }
	&__title {
		text-transform: uppercase;
		&:after {
			display: block;
			content: '';
			width: 106px;
			height: 2px;
			background-color: #6ac0f3;
			margin-top: 5px;
			margin-bottom: 5px; } }
	.logo {
		-webkit-align-self: flex-start;
		align-self: flex-start;
		margin-right: 20px;
		border-color: #6ac0f3;
		@include r(820) {
			display: none; } }
	&__item {
		margin: 0 20px 20px 0;
		@include r(720) {
			margin-right: 40px; }
		&::last-child {
			margin-right: 0; }
		@include r(510) {
			display: none; } }
	&__list {
		li {}
		a {
			color: #dcdcdc;
			font-family: Akrobat;
			font-size: 16px;
			text-decoration: none; } }
	&__contacts {
		@include clr;
		text-align: justify;
		font-size: 30px;
		margin-bottom: 10px;
		@include r(820) {
			display: none; }
		@include r(510) {
			width: 100%;
			position: relative;
			padding: 25px 25px 38px; } }
	&__phone {
		display: inline;
		color: #dcdcdc;
		font-family: Akrobatbold;
		margin-bottom: 5px;
		float: left;
		@include r(510) {
			display: inline-block;
			margin-right: 20px; } }
	&__hour-work {
		margin-bottom: 5px;
		position: absolute;
		text-align: center;
		left: 0;
		right: 0;
		top: 55px; }
	&__email {
		display: inline-block;
		margin-bottom: 5px;
		font-family: Akrobatbold;
		float: right;
		@include r(510) {
			display: inline-block; } }
	&__bottom-wrap {
		display: flex;
		-webkit-justify-content: space-between;
		justify-content: space-between; }
	&__bottom-title {
		font-family: Akrobatbold;
		font-size: 20px;
		text-align: center;
		text-transform: uppercase;
		margin-bottom: 10px; }
	&__bottom-vygoda {
		width: 50%;
		text-align: center;
		color: #dcdcdc;
		font-size: 18px;
		text-transform: uppercase;
		font-family: Akrobat;
		text-align: center; }
	&__bottom-hotite {
		width: 50%;
		color: #dcdcdc;
		font-family: Akrobat;
		font-size: 18px;
		text-transform: uppercase;
		margin-bottom: 5px;
		text-align: center;
		text-transform: uppercase;
		button {
			margin-top: 15px;
			font-size: 16px;
			padding: 10px 35px;
			border-radius: 15px;
			background-color: transparent;
			text-transform: uppercase;
			border: 1px solid #6ac0f3;
			color: #dcdcdc; } } }
.copyright {
	background-color: #525252;
	color: #dcdcdc;
	font-size: 14px;
	padding: 10px 0;
	@include clr;
	@include r(540) {
		font-size: 12px; }
	@include r(470) {
		font-size: 10px; }
	&__inn {
		float: left;
		width: 50%;
		padding-top: 15px;
		@include r(710) {
			width: auto;
			margin-right: 10px; } }
	&__firma {
		padding-top: 15px;
		float: left;
		margin-bottom: 10px;
		margin-right: 10px; }
	&__text {
		clear: both;
		font-size: 14px;
		text-align: center;
		padding-bottom: 20px; }
	&__counter {
		float: right; } }
.counter {
	border: 3px solid #dcdcdc;
	display: inline-block;
	padding: 3px 3px 3px 50px;
	position: relative;
	svg {
		position: absolute;
		left: 3px;
		top: 3px;
		font-size: 30px; }
	span {
		color: #dcdcdc;
		display: block;
		font-size: 10px;
		font-family: sans-serif;
		text-align: right; } }
