.comands {
	padding: 80px 0;
	@include r(500) {
		padding: 40px 0 10px; }
	.container {
		max-width: 1000px; }
	@include r(700) {
		margin-top: 0; }
	&__title {
		text-align: center;
		color: #1f1f1f;
		font-family: Akrobat;
		font-size: 50px;
		font-weight: 400;
		text-transform: uppercase;
		margin-bottom: 30px;
		@include r(530) {
			font-size: 30px;
			margin-bottom: 10px; } }
	&__list {
		display: flex;
		justify-content: center; }
	&__item {
		width: 25%;
		text-align: center;
		background-color: #e8e8e8;
		text-align: center;
		padding-bottom: 48px;
		border-radius: 4px;
		translate: all 1s liner;
		&:hover {
			transform: scale(1.1);
			translate: all 4s liner; }
		@include r(768) {
			padding-bottom: 30px; } }
	&__item + &__item {
		margin-left: 15px; }
	&__image {
		margin: 17px auto 35px;
		max-width: 141px;
		width: 90%;
		overflow: hidden;
		background-color: transparent;
		border-radius: 19px;
		overflow: hidden;
		@include r(768) {
			margin-bottom: 17px; }
		img {
			width: 100%;
			border-radius: 19px; } }
	&__caption {
		padding: 17px 0;
		background-color: #747474;
		@include r(425) {
			height: 80px; } }
	&__name {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 20px;
		text-transform: uppercase;
		@include r(768) {
			font-size: 16px; }
		@include r(425) {
			font-size: 14px; }

		&:after {
			display: block;
			content: '';
			width: 130px;
			height: 2px;
			background-color: #3eb5f1;
			margin: 15px auto 19px;
			@include r(768) {
				display: none; } } }
	&__position {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 13px;
		font-weight: 700;
		@include r(425) {
			font-size: 12px; } } }

