@include font('LucidaSans', 'LucidaSansUnicode');
body, html {
	width: 100%;
	margin: 0px;
	padding: 0; }
body {
    -ms-text-size-adjust: none;
    -moz-text-size-adjust: none;
    -webkit-text-size-adjust: none; }

@font-face {
    font-family: 'KobzarKS';
    src: url('../fonts/KobzarKS.woff2') format('woff2'), url('../fonts/KobzarKS.woff') format('woff');
    font-weight: normal;
    font-style: normal; }

.container {
	max-width: 1200px;
	width: 90%;
	margin: 0 auto;
	@include r(425) {
		width: 95%; } }
.top-button {
	display: block;
	position: fixed;
	bottom: 10px;
	right: 80px;
	text-decoration: none;
	background-color: #000;
	color: #fff;
	opacity: 0.6;
	z-index: 10;
	padding: 28px 5px 5px 5px;
	vertical-align: bottom;
	font-size: 16px;
	text-transform: lowercase;
	&:after {
		display: block;
		content: '';
		position: absolute;
		top: 5px;
		left: 50%;
		width: 0;
		height: 0;
		border-style: solid;
		margin-left: -23px;
		border-width: 0 23px 23px 23px;
		border-color: transparent transparent #ffffff transparent; } }
img {
	transition: transform 1s; }



