.goods {
	padding: 40px 0;
	.container {
		max-width: 960px; }
	@include r(375) {
		padding: 20px 0; }
	&__title {
		text-align: center;
		color: #1f1f1f;
		font-family: Akrobat;
		font-size: 60px;
		text-transform: uppercase;
		color: #010101;
		margin-bottom: 30px;
		@include r(500) {
			font-size: 40px;
			margin-bottom: 20px; }
		@include r(375) {
			font-size: 30px;
			margin-bottom: 15px; }
		span {
			color: #4890bd; } }
	&__images {
		font-size: 0;
		display: flex;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		margin-bottom: 40px; }
	&__image {
		position: relative;
		overflow: hidden;
		&:hover img {
			transform: scale(1.1); }
		&:after {
			display: block;
			content: '';
			position: absolute;
			top: 10px;
			bottom: 10px;
			right: 10px;
			left: 10px;
			border: 1px solid #fff; } }
	&__image-wrap {
		width: 48.5%;
		overflow: hidden;
		img {
			width: 100%; }
		&_small {
			display: flex;
			flex-direction: column;
			justify-content: space-between; } }
	&__sale {
		position: absolute;
		font-size: 20px;
		color: #FFF;
		top: 4px;
		left: -52px;
		width: 250px;
		box-shadow: 2px 3px 7px rgba(0, 0, 0, 0.35);
		background-color: #ffd357;
		transform: rotate(-30deg);
		padding: 10px 0;
		text-align: center;
		color: #3b3b3b;
		font-family: "Lucida Sans Unicode";
		font-size: 37px;
		font-weight: 400;
		text-transform: uppercase;
		@include r(800) {
			font-size: 16px;
			text-align: center;
			box-shadow: none;
			top: 7px;
			left: -20px;
			padding: 5px;
			width: 125px; } }




	&__description {
		@include clr;
		position: relative; }
	&__table {
		float: left;
		width: 48.5%;
		@include r(425) {
			width: 57%; }
		&-head {
			font-family: Akrobatbold;
			background-color: #dcdcdc;
			text-transform: uppercase;
			/*semi bold*/ }
		td {
			padding: 17px 0;
			border: 1px solid #000000;
			text-align: center;
			color: #3b3b3b;
			font-family: Akrobat;
			font-size: 26px;
			@include r(800) {
				font-size: 16px;
				padding: 5px 0; }
			@include r(425) {
				font-size: 14px; } }
		td.active {
			background-color: #ffd357;
			span {
				color: #d51a1a; } } }
	&__button {
		float: right;
		padding: 14px 22px;
		border: 2px solid #4890bd;
		text-align: center;
		border-radius: 20px;
		background-color: transparent;
		color: #3b3b3b;
		font-family: Akrobat;
		/*semi bold*/
		font-size: 28px;
		text-transform: uppercase;
		position: absolute;
		bottom: 0;
		right: 0;
		@include r(800) {
			font-size: 16px; }
		@include r(425) {
			border-radius: 12px;
			padding: 7px 5px;
			font-size: 14px; } } }





